<template>
  <div style="width: 99%">
    <el-container>
      <el-main style="width: 75%">
        <router-view/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'test',
  watch: {
    // 监控当前页面path，防止刷新页面显示错误
    '$route.path': {
      deep: true,
      immediate: true,
      handler(to, from) {
        console.log('to: ' + to)

      }
    }
  },
};
</script>

<style scoped>

</style>



